<template>
  <scrollable-content>
    <template #header>
      <h2>todos</h2>
    </template>
    <template #scrollable_content>
      <b-container>
        <transition-group name="flip-list" tag="div" class="todos row">
          <b-col
            cols="12"
            v-for="item in sortedTodos"
            :key="item.id"
            class="p-0 py-2"
          >
            <TodoItem
              :item="item"
              @completeTodo="(id) => completeTodo(id)"
              @deleteTodo="(id) => deleteTodo(id)"
            />
          </b-col>
        </transition-group>
      </b-container>
    </template>
  </scrollable-content>
</template>

<script>
import { createNamespacedHelpers } from "vuex";
import TodoItem from "../components/Todo/TodoItem.vue";
const { mapState, mapActions } = createNamespacedHelpers("todos");
export default {
  name: "Todos",
  components: {
    TodoItem,
  },
  computed: {
    ...mapState(["userTodos"]),
    sortedTodos() {
      const copy = JSON.parse(JSON.stringify(this.userTodos));
      return copy.sort((a, b) => (a.completed > b.completed ? 1 : -1));
    },
  },
  methods: {
    ...mapActions(["fetchUserTodos", "completeTodo", "deleteTodo"]),
  },
  created() {
    this.fetchUserTodos(this.$route.params.id);
  },
};
</script>
<style lang="scss" scoped>
.todos {
  max-width: 600px;
  margin: 0 auto;
}
.flip-list-move {
  transition: transform 1s;
}
</style>
